<template>
  <div class="box">
    <div class="nav">
      <p>ele.me</p>
      <router-link to="/login"><p>登录/注册</p></router-link>
    </div>
    <div class="ner">
      <p>当前定位城市：</p>
      <p>定位不准时，请在城市列表中选择</p>
    </div>
    <hr />
    <div class="bot">
      <p class="wen">北京</p>
      <p>></p>
    </div>

    <div class="city">
      <h4>热门城市</h4>
      <hr />

      <ul>
        <li v-for="(item,index) in list" :key="index">{{ item.name }}</li>
      </ul>
    </div>

    <div class="country" v-for="(item,index) in city" :key="index">
        <p>{{index}}</p>
        <ul v-for="(ite,ind) in item" :key="ind">
            <li @click="jump(ite)">{{ ite.name }}</li>
        </ul>
    

      <!-- <ul>
        <li v-for="(item,key) in city" :key="key">
            {{ key }}
            
        </li>
      </ul>  -->
     
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      city: [],

    };
  },
  created() {},
  mounted() {
    this.$http.get("http://elm.cangdu.org/v1/cities?type=hot").then(res => {
      //  console.log(res)
      this.list = res.data;
    });
    this.$http.get("http://elm.cangdu.org/v1/cities?type=group").then(res => {
    //   console.log(res);
      this.city = res.data;
    });

     
  },
  methods: {
     jump(b){
     
      // console.log(b)
        // this.$http.get(`http://elm.cangdu.org/v1/cities/${b.id}`).then(res => {
            // console.log(res)
        // this.country = res.data;
        console.log(b)
        this.$router.push({
          path:'/city',
          query:{
           name:b.name,
           id:b.id
            
          }
        })
    // });
     }
  }
};
</script>

