<template>
    <div>
        <!-- 城市搜索 -->
        <div class="head">
            <span  @click="fan"><i class="el-icon-arrow-left"></i></span>
            <span>{{ $route.query.name }}</span>
            <span>切换城市</span>
        </div>
        <div class="ge">
            <input type="text" v-model="value" placeholder="输入学校 商务楼 地址">
            <button @click="ajaxcity()">提交</button>
        </div>

        <!-- 搜索展示 -->
        <ul  class="cun" v-show="!flag">
           <li @click="add_list(i)"  v-for="(i,k) in arr" :key="k">{{ i.name }}</li>
       </ul>

      
          
          <ul class="cun" v-show="flag"> 
              <h3>搜索历史</h3>
               <li  v-for="(i,k) in brr" :key="k" >{{ i.name }}</li>
          </ul>

      
    </div>
</template>

<script>
export default {
    data() {
        return {
          country:"",
          value:'',
          arr:[],
          flag:true,
          brr:JSON.parse(localStorage.getItem("history_list"))||[]
        };
    },
    created() {

    },
    mounted() {
    //    接收城市数据
      
    //  this.country = this.$route.query.data.name;
     console.log(this.$route.query.data)
       
    },
    methods: {
         async ajaxcity(){
            this.flag=false
             let { data:res } = await this.$http.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.$route.query.id}&keyword=${this.value}`)
             console.log(res)
             this.arr = res
         },

         fan(){
            this.$router.push('/')
         },

         add_list(i){
             this.flag=true
             this.brr.unshift(i)
             localStorage.setItem("history_list",JSON.stringify(this.brr))
             this.$router.push('/list')

         }
    }
};
</script>

<style lang="scss" scoped>
.head{
    width: 100%;
    height: 50px;
    background-color: blue;
    color: white;
    display: flex;
    justify-content: space-between;
    font-size: 22px;
    /* text-align: center; */
    align-items: center;
}
.ge{
    width: 100%;
    height: 150px;
    text-align: center;
}
.ge>input{
    width: 80%;
    height: 30px;
    margin-top: 10px;
}
.ge>button{
    width: 80%;
    height: 30px;
    margin-top: 12px;
    background-color: blue;
    outline: none;
    color: white;
}
.cun{
    width: 100%;
    
}.cun>li{
        width: 100%;
        height: 40px;
        border-bottom: 1px solid blue;
        text-align: center;
        line-height: 40px;
    }
</style>
