<script>
export default {
  data() {
    return {
      active: 0,
      list_one: "",
      list_two:"",
      unn:""
    };
  },
  created() {},
  mounted() {
    this.$http
      .get(
        "http://elm.cangdu.org/v2/index_entry?geohash=45.788955,126.641903&group_type=1&flags[]=F"
      )
      .then(res => {
        console.log(res);

        this.list_one = res.data.slice(0,8);
        this.list_two = res.data.slice(8,16);
      });

      this.$http.get("http://elm.cangdu.org/shopping/restaurants?latitude=41.083584&longitude=122.982404&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=")
      .then((res)=>{
          console.log(res)
          this.unn = res.data
      })
  },
  methods: {}
};
</script>

